header{
    width: 100%;
    background-color: azure;
    display: flex;
    justify-content: center;
}
.header-container{
    max-width: 1240px;
    width: 100%;
    margin-left: 40px;
    margin-right: 40px;
    display: flex;
    justify-content: space-between;
    background-color: aqua;
    position: relative;
}
.header-container .menu-lang{
    position: absolute;
    left: 0;    
}
.header-container .menu-icon {
    /* position: absolute;
    right: 0; */
    position: fixed;
    padding: 0.5rem 0.75rem;
    font-size: 1.3rem;
    line-height: 1;
    background-color: #2a26264d;
    border: none;
    color: var(--color-white);
    top: 15px;
    right: 15px;
    display: block;
    border-radius: 5px;
    cursor: pointer;
    z-index: 99;
}
.header-container .menu-lang{
    /* position: absolute;
    right: 0; */
    position: fixed;
    padding: 0.5rem 0.75rem;
    font-size: 1.3rem;
    line-height: 1;
    background-color: #2a26264d;
    border: none;
    color: var(--color-white);
    top: 15px;
    left: 15px;
    display: block;
    border-radius: 5px;
    cursor: pointer;
    z-index: 99;
}

.sideBar{
    height: 100%;
    width: 380px;
    /* margin-right: 0px; */
    /* margin-right: -381px; */
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    /* opacity: 0; */
    background-color: var(--color-sidebar);
    overflow-x: hidden;
    padding-top: 1vh;
    transition: all ease-in-out 0.5s;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    z-index: 9999;
}
