.close-btn{
    width: 100%;
    text-align: end;
    padding: 0 10px;
}
.close-icon{
    color: var(--color-orange);
    font-size: 23px;
    cursor: pointer;
}

.menu-body{
    width: 100%;
}
.menu-body-first{
    display: flex;
    border-bottom: 1px solid #ccc !important;
    background-color: transparent;
    align-items: center;
    padding: 4px;
}
.menu-body-first h4{
    color: var(--color-orange);
}
.menu-body-first .phone{
    color: var(--color-black);
}
.menu-body-first img{
    width: 106px;
    height: 86px;
    margin: 10px;
    margin-left: 70px;
}

.menu-body-second{
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid #ccc !important;
    background-color: transparent;
    align-items: center;
    padding: 10px;
    color: var(--color-black);
}
.menu-body-second h4{
    font-size: 1rem;
    font-weight: 400;
}
.menu-body-second p{
    font-size: 0.8rem;
}
.menu-body-second span{
    font-size: 1.5rem;
}

.menu-body-icons{
    display: flex;
    justify-content: center;
    background-color: transparent;
    align-items: center;
    padding: 10px;
    color: var(--color-black);
}
.menu-body-icons span{
    margin: 0 10px;
}
.menu-body-icons span img{
    width: 30px;
    height: 30px;
    border-radius: 50%;
    object-fit: cover;
}