footer{
    width: 100%;
    background-color: var(--color-footer);
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 30px 30px 10px 30px ;

}
footer img{
    width: 100px;
    margin-bottom: 20px;
}
footer hr{
    height: 2px;
    width: 100%;
    background-color: var(--color-white);
    border: none;
}
footer h4{
    font-size: 15px;
    font-weight: 500;
    color: var(--color-text);
}
footer h4 a{
    font-size: 17px;
    color: var(--color-black);
    font-weight: 600;
}